"use client";
import React, { useActionState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import {
  Field,
  FieldDescription,
  FieldGroup,
  FieldLabel,
  FieldLegend,
} from "./ui/field";
import { Input } from "./ui/input";
import { Button } from "./ui/button";
import Link from "next/link";
import { actions } from "@/actions";
import { FormState } from "@/validations/auth";

const INITIAL_STATE: FormState = {
  data: {
    email: "",
    password: "",
  },
  success: false,
  message: "",
};

const SignUpForm: React.FC = () => {
  const [formState, formAction] = useActionState(
    actions.auth.registerUserAction,
    INITIAL_STATE
  );
  console.log(formState);

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle>Registrate</CardTitle>
      </CardHeader>
      <CardContent>
        <form action={formAction}>
          <FieldGroup>
            <Field>
              <FieldLabel htmlFor="email">Email</FieldLabel>
              <Input
                id="email"
                name="email"
                type="email"
                placeholder="example@email.com"
                required
                className="h-12 mb-2"
              />
            </Field>
          </FieldGroup>
          <FieldGroup>
            <Field>
              <FieldLabel htmlFor="password">Password</FieldLabel>
              <Input
                id="password"
                name="password"
                type="password"
                placeholder="*************"
                required
                className="h-12 mb-2"
              />
            </Field>
          </FieldGroup>
          <FieldGroup className="mt-2 flex items-center justify-evenly">
            <Field orientation={"responsive"}>
              <Button type="submit" color="primary">
                Crear Cuenta
              </Button>
              <FieldLegend>Ya posee una cuenta?</FieldLegend>
              <FieldDescription>
                <Link href={"/signin"} className="text-gray-600 font-medium">
                  Ingres√°
                </Link>
              </FieldDescription>
            </Field>
          </FieldGroup>
        </form>
      </CardContent>
    </Card>
  );
};

export default SignUpForm;
